import { IPublicTypeJSONObject } from '@alilc/lowcode-types';
import { utils as lowcodeUtils } from '@alilc/lowcode-renderer-core';
import { host } from '../host';

export const parseContantsSchema = () => {
  const constants: { [key: string]: any } = {};
  const schema = host.project.getSchema();
  Object.keys(schema.constants || {}).forEach((key) => {
    try {
      const constantsItem = schema.constants?.[key] as IPublicTypeJSONObject;
      const jsExpression = { ...constantsItem };
      if (constantsItem.mock) {
        jsExpression.value = constantsItem.mock;
      }
      constants[key] = lowcodeUtils.parseThisRequiredExpression(jsExpression, constants);
    } catch (error) {
      // eslint-disable-next-line no-console
      console.warn(`parse constants '${key}' failed`, error);
    }
  });
  return constants;
};
